import "core-js/modules/es.array.push.js";
import { API_REQUEST_HEADERS, STORAGE_API_BASE_URL } from '@/utils/constants';
import { MediaTypes, ThumbnailFormats } from '@/utils/enums';
import { compressImage } from '@/utils/functions';
import SettingsMixin from '@/mixins/settings';
import { MEDIA_CONTEXT } from '@/store/modules/database/media';
import { mapActions } from 'vuex';
export default {
  name: 'UploadFiles',
  mixins: [SettingsMixin],
  props: {
    folder: {
      type: String,
      default: 'medias'
    },
    maxUploadSize: {
      type: Number,
      default: 256
    },
    baseUrl: {
      type: String,
      default: STORAGE_API_BASE_URL
    },
    headers: {
      type: Object,
      default: API_REQUEST_HEADERS
    },
    acceptImages: {
      type: Boolean,
      default: true
    },
    acceptVideos: {
      type: Boolean,
      default: true
    },
    acceptAudios: {
      type: Boolean,
      default: true
    },
    acceptDocuments: {
      type: Boolean,
      default: true
    },
    acceptOtherTypes: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      fileList: [],
      percentage: 0,
      failedList: [],
      uploadedList: [],
      overwrite: true,
      isUploading: false,
      uploadedFileName: '',
      docExtList: ['DOC', 'DOCX', 'HTML', 'HTM', 'ODT', 'PDF', 'XLS', 'XLSX', 'ODS', 'PPT', 'PPTX', 'TXT'],
      vidExtList: ['WEBM', 'MP4', 'AVI', 'MOV', 'FLV', 'OGG', 'WMV', 'MKV'],
      imgExtList: ['JPEG', 'JPG', 'PNG', 'GIF', 'WEBP', 'SVG'],
      audExtList: ['MP3', 'WAV', 'M4A', 'WMA', 'AAC', 'FLAC']
    };
  },
  computed: {
    acceptedExtensions() {
      const extensions = [];
      if (this.acceptImages && this.acceptAudios && this.acceptVideos && this.acceptDocuments && this.acceptOtherTypes) return '*';
      if (this.acceptImages) {
        this.imgExtList.forEach(ext => {
          extensions.push('.' + ext.toLowerCase());
        });
      }
      if (this.acceptAudios) {
        this.audExtList.forEach(ext => {
          extensions.push('.' + ext.toLowerCase());
        });
      }
      if (this.acceptVideos) {
        this.vidExtList.forEach(ext => {
          extensions.push('.' + ext.toLowerCase());
        });
      }
      if (this.acceptDocuments) {
        this.docExtList.forEach(ext => {
          extensions.push('.' + ext.toLowerCase());
        });
      }
      return extensions.join(', ');
    }
  },
  methods: {
    ...mapActions('media', ['generateThumbnail']),
    format(percentage) {
      return percentage === 100 ? this.$t('completed') : `${percentage}%`;
    },
    handleChange(file, fileList) {
      this.fileList = fileList;
    },
    getFileTypeByName(name) {
      const fileExt = name.split('.').pop();
      if (fileExt) {
        if (this.docExtList.includes(fileExt.toUpperCase())) return MediaTypes.DOCUMENT.value;
        if (this.vidExtList.includes(fileExt.toUpperCase())) return MediaTypes.VIDEO.value;
        if (this.imgExtList.includes(fileExt.toUpperCase())) return MediaTypes.IMAGE.value;
        if (this.audExtList.includes(fileExt.toUpperCase())) return MediaTypes.AUDIO.value;
      }
      return MediaTypes.OTHERS.value;
    },
    async send() {
      let pass = true;
      for (let i = 0; i < this.fileList.length; i++) {
        const sizeLessThan = this.fileList[i].size / 1024 / 1024 < this.maxUploadSize;
        if (!sizeLessThan) {
          pass = false;
          this.$message.error(this.$t('uploadFileSizeWarning', [this.fileList[i].name, this.maxUploadSize]));
        }
        const fileType = this.getFileTypeByName(this.fileList[i].name);
        const formatNotAllowed = !this.acceptImages && fileType == MediaTypes.IMAGE.value || !this.acceptVideos && fileType == MediaTypes.VIDEO.value || !this.acceptAudios && fileType == MediaTypes.AUDIO.value || !this.acceptDocuments && fileType == MediaTypes.DOCUMENT.value || !this.acceptOtherTypes && fileType == MediaTypes.OTHERS.value;
        if (formatNotAllowed) this.$message.error(this.$t('fileFormatNotAllowed', [this.fileList[i].name]));
        if (pass && formatNotAllowed) pass = false;
      }
      if (!pass) return;
      this.isUploading = true;
      for (let i = 0; i < this.fileList.length; i++) {
        const newFileName = this.fileList[i].name;
        var file = this.fileList[i].raw;
        if (this.compressImages) file = await compressImage(file, {
          maxSizeMB: this.compressThreshold
        });
        const formData = new FormData();
        formData.append('folder', this.folder);
        formData.append('overwrite', this.overwrite);
        formData.append('file', file, newFileName);
        try {
          this.percentage = 0;
          this.uploadedFileName = this.fileList[i].name;
          const task = await this.axios.post(this.baseUrl, formData, {
            headers: this.headers,
            onUploadProgress: progressEvent => {
              if (progressEvent.total !== null) this.percentage = Math.round(progressEvent.loaded * 100 / progressEvent.total);
            }
          });
          if (task.data && task.data['status'] == 'success') {
            this.uploadedList.push(this.fileList[i].name);
            await this.addItemInDB({
              context: MEDIA_CONTEXT,
              data: {
                name: newFileName,
                link: 'root/' + this.folder + '/' + newFileName,
                type: this.getFileTypeByName(newFileName),
                size: file.size || 0,
                mime: file.type || '',
                sender: this.userData.id
              }
            });
            if (this.getFileTypeByName(newFileName) == MediaTypes.IMAGE.value) {
              await this.generateThumbnail({
                autoWidth: this.thumbnailsFormat == ThumbnailFormats.AUTOWIDTH.value,
                autoHeight: this.thumbnailsFormat == ThumbnailFormats.AUTOHEIGHT.value,
                folder: this.folder,
                file: newFileName,
                lib: this.imgProcessingLib,
                models: this.thumbnails.filter(thumbnail => thumbnail.label && thumbnail.width && thumbnail.height)
              });
            }
          } else if (task.data && task.data['status'] == 'exists') {
            this.failedList.push(this.fileList[i].name);
            this.$message({
              type: 'error',
              duration: 5000,
              showClose: true,
              message: this.$t('fileExistsError', [this.fileList[i].name])
            });
          } else {
            this.failedList.push(this.fileList[i].name);
            this.$message.error(this.$t('fileSendingError', [this.fileList[i].name]));
          }
        } catch (error) {
          this.failedList.push(this.fileList[i].name);
          this.$message.error(this.$t('fileSendingError', [this.fileList[i].name]));
        }
        this.uploadedFileName = '';
      }
      setTimeout(() => {
        this.$message({
          showClose: true,
          duration: 5000,
          message: this.$t('sendingCompleted', [this.uploadedList.length, this.failedList.length])
        });
        this.clearFileList();
        this.isUploading = false;
      }, 1000);
    },
    clearFileList() {
      this.fileList = [];
      this.failedList = [];
      this.uploadedList = [];
    }
  }
};