import "core-js/modules/es.array.push.js";
import { MediaTypes } from '@/utils/enums';
export default {
  name: 'AddFromAnUrl',
  props: {
    multiple: {
      type: Boolean,
      default: false
    },
    limit: {
      type: Number,
      default: 0
    },
    acceptImages: {
      type: Boolean,
      default: true
    },
    acceptVideos: {
      type: Boolean,
      default: true
    },
    acceptAudios: {
      type: Boolean,
      default: true
    },
    acceptDocuments: {
      type: Boolean,
      default: true
    },
    acceptOtherTypes: {
      type: Boolean,
      default: true
    }
  },
  emits: ['validate'],
  data() {
    return {
      items: [{
        url: 'http://'
      }],
      docExtList: ['DOC', 'DOCX', 'HTML', 'HTM', 'ODT', 'PDF', 'XLS', 'XLSX', 'ODS', 'PPT', 'PPTX', 'TXT'],
      vidExtList: ['WEBM', 'MP4', 'AVI', 'MOV', 'FLV', 'OGG', 'WMV', 'MKV'],
      imgExtList: ['JPEG', 'JPG', 'PNG', 'GIF', 'WEBP', 'SVG'],
      audExtList: ['MP3', 'WAV', 'M4A', 'WMA', 'AAC', 'FLAC']
    };
  },
  computed: {
    isUrlsValid() {
      const u = this.items.filter(item => item.url && this.$validator.isURL(item.url));
      return u.length === this.items.length;
    }
  },
  methods: {
    manageItems(index) {
      if (index > 0) this.items.splice(index, 1);else this.items.push({
        url: 'http://'
      });
    },
    getFileTypeByUrl(url) {
      const fileExt = url.split('.').pop();
      if (fileExt) {
        if (this.docExtList.includes(fileExt.toUpperCase())) return MediaTypes.DOCUMENT.value;
        if (this.vidExtList.includes(fileExt.toUpperCase())) return MediaTypes.VIDEO.value;
        if (this.imgExtList.includes(fileExt.toUpperCase())) return MediaTypes.IMAGE.value;
        if (this.audExtList.includes(fileExt.toUpperCase())) return MediaTypes.AUDIO.value;
      }
      return MediaTypes.OTHERS.value;
    },
    validate() {
      if (this.isUrlsValid) {
        let pass = true;
        const links = [];
        for (let i = 0; i < this.items.length; i++) {
          const fileType = this.getFileTypeByUrl(this.items[i].url);
          const formatNotAllowed = !this.acceptImages && fileType == MediaTypes.IMAGE.value || !this.acceptVideos && fileType == MediaTypes.VIDEO.value || !this.acceptAudios && fileType == MediaTypes.AUDIO.value || !this.acceptDocuments && fileType == MediaTypes.DOCUMENT.value || !this.acceptOtherTypes && fileType == MediaTypes.OTHERS.value;
          if (formatNotAllowed) this.$message.error(this.$t('fileFormatNotAllowed', [this.items[i].url]));
          if (pass && formatNotAllowed) pass = false;
          links.push(this.items[i].url);
        }
        if (!pass) return;
        this.$emit('validate', this.multiple ? links : links[0], this.multiple);
      }
    }
  }
};