import "core-js/modules/es.array.push.js";
import { STORAGE_API_BASE_URL } from '@/utils/constants';
import AddFromAnUrl from './AddFromAnUrl.vue';
import MediaLibrary from './MediaLibrary.vue';
import UploadFiles from './UploadFiles.vue';
export default {
  name: 'MediaPicker',
  components: {
    UploadFiles,
    MediaLibrary,
    AddFromAnUrl
  },
  props: {
    enableUploadFiles: {
      type: Boolean,
      default: true
    },
    enableMediaLibrary: {
      type: Boolean,
      default: true
    },
    enableAddFromAnUrl: {
      type: Boolean,
      default: true
    },
    displayImages: {
      type: Boolean,
      default: true
    },
    displayVideos: {
      type: Boolean,
      default: true
    },
    displayAudios: {
      type: Boolean,
      default: true
    },
    displayDocuments: {
      type: Boolean,
      default: true
    },
    displayOtherTypes: {
      type: Boolean,
      default: true
    },
    multiple: {
      type: Boolean,
      default: false
    },
    limit: {
      type: Number,
      default: 0
    },
    uploadConfig: {
      type: Object,
      default: () => ({
        folder: 'medias',
        maxUploadSize: 256,
        baseUrl: STORAGE_API_BASE_URL
      })
    },
    currentTab: {
      type: String,
      default: '2'
    },
    initSelectedMedias: {
      type: Array,
      default: null
    },
    enableSelectionDetails: {
      type: Boolean,
      default: false
    }
  },
  emits: ['selected'],
  data() {
    return {
      tab: this.currentTab
    };
  },
  methods: {
    handleClick() {
      /* empty */
    },
    validateSelection(selection, isMultiple) {
      if (isMultiple) {
        const items = [];
        selection.forEach(element => {
          if (this.enableSelectionDetails) items.push(element);else items.push(element.link);
        });
        this.$emit('selected', items);
      } else this.$emit('selected', this.enableSelectionDetails ? selection : selection.link);
    },
    validateLinks(links, isMultiple) {
      if (isMultiple) this.$emit('selected', links);else this.$emit('selected', links[0]);
    }
  }
};