import useClipboard from 'vue-clipboard3';
export default {
  name: 'Copy',
  props: {
    toCopy: {
      type: String,
      default: ''
    }
  },
  emits: ['copy'],
  setup(props, context) {
    const {
      toClipboard
    } = useClipboard();
    const copy = async () => {
      try {
        await toClipboard(props.toCopy);
        context.emit('copy', props.toCopy, true);
      } catch (e) {
        console.error(e);
        context.emit('copy', props.toCopy, false);
      }
    };
    return {
      copy
    };
  }
};