import { ref } from 'vue';
import "core-js/modules/es.array.push.js";
import { formatDate, reformatDate } from '@/utils/chronos';
import { Routes } from '@/utils/enums';
import AccessManager from '@/mixins/access-manager';
import SettingsMixin from '@/mixins/settings';
import ViewsBaseMixin from '@/mixins/views-base';
import { AD_CONTEXT } from '@/store/modules/database/ad';
import { mapActions } from 'vuex';
export default {
  name: 'Ad',
  mixins: [ViewsBaseMixin, AccessManager, SettingsMixin],
  data() {
    return {
      Routes,
      adsIds: [],
      ad: {
        pid: '',
        data: [],
        title: '',
        content: '',
        display: 0,
        broadcastRange: ''
      },
      rules: {
        pid: [{
          required: true,
          message: this.$t('fieldRequired'),
          trigger: 'blur'
        }],
        title: [{
          required: true,
          message: this.$t('fieldRequired'),
          trigger: 'blur'
        }],
        content: [{
          required: true,
          message: this.$t('fieldRequired'),
          trigger: 'blur'
        }],
        display: [{
          required: true,
          message: this.$t('fieldRequired'),
          trigger: 'blur'
        }],
        broadcastRange: [{
          required: true,
          message: this.$t('fieldRequired'),
          trigger: 'blur'
        }]
      },
      currentStoFolder: 'ads',
      currentEditItem: null,
      pickerVisible: false,
      isLoading: false,
      adDialog: false,
      tableData: [],
      pageSize: 6,
      search: '',
      total: 0,
      page: 1
    };
  },
  computed: {
    pagedTableData() {
      this.total = this.searching.length;
      return this.searching.slice(this.pageSize * this.page - this.pageSize, this.pageSize * this.page);
    },
    searching() {
      if (!this.search) {
        this.total = this.tableData.length;
        return this.tableData;
      }
      this.page = 1;
      return this.tableData.filter(data => data.title.toLowerCase().includes(this.search.toLowerCase()) || data.pid.toLowerCase().includes(this.search.toLowerCase()) || data.broadcast_start.toLowerCase().includes(this.search.toLowerCase()) || data.broadcast_end.toLowerCase().includes(this.search.toLowerCase()));
    }
  },
  created() {
    this.init();
  },
  methods: {
    ...mapActions('ad', ['getAllIdentifiers', 'getAllAds']),
    reformatDate,
    setPage(val) {
      this.page = val;
    },
    indexMethod(index) {
      return index + (this.page - 1) * this.pageSize + 1;
    },
    async init() {
      this.isLoading = true;
      const resp0 = await this.getAllIdentifiers();
      if (resp0.data) this.adsIds = resp0.data;
      const resp = await this.getAllAds();
      if (resp.data) this.tableData = resp.data;
      if (resp.failure) this.$message.error(this.$t('unableToUpdateData'));
      this.isLoading = false;
    },
    addItem() {
      this.currentEditItem = null;
      this.ad.pid = '';
      this.ad.data = [];
      this.ad.title = '';
      this.ad.content = '';
      this.ad.display = 0;
      this.ad.broadcastRange = '';
      this.updateAdData();
      this.adDialog = true;
    },
    editItem(index, item) {
      this.currentEditItem = item;
      this.ad.pid = item.pid;
      this.ad.data = JSON.parse(item.data);
      this.ad.title = item.title;
      this.ad.content = item.content;
      this.ad.display = parseInt(item.display);
      const start = item.broadcast_start.split('-'),
        end = item.broadcast_end.split('-');
      if (start.length === 3 && end.length === 3) {
        this.ad.broadcastRange = [new Date(start[0], parseInt(start[1]) - 1, start[2]), new Date(end[0], parseInt(end[1]) - 1, end[2])];
      }
      this.updateAdData();
      this.adDialog = true;
    },
    deleteItem(index, item) {
      this.$confirm(this.$t('deleteAdQuestion'), this.$t('delete'), {
        confirmButtonText: this.$t('yes'),
        cancelButtonText: this.$t('cancel'),
        type: 'warning'
      }).then(async () => {
        const task = await this.deleteItemInDB({
          context: AD_CONTEXT,
          id: item.id
        });
        if (task.success) {
          this.$message.success(this.$t('success'));
          this.init();
        } else {
          this.$message.error(this.$t('anErrorOccured'));
        }
      }).catch(() => {
        /* empty */
      });
    },
    clearContent() {
      this.ad.content = '';
    },
    async saveContent(dataUrl) {
      this.ad.content = dataUrl;
      this.pickerVisible = false;
    },
    submitForm(ref) {
      this.$refs[ref].validate(async valid => {
        if (valid) {
          const adId = this.adsIds.find(adId => adId.id == this.ad.pid);
          if (ref === 'addForm') {
            if (this.tableData.findIndex(ad => ad.pid == this.ad.pid) > -1 && !parseInt(adId?.reusable)) {
              this.$message.error(this.$t('samePidError'));
              return false;
            }
            const task = await this.addItemInDB({
              context: AD_CONTEXT,
              data: {
                pid: this.ad.pid,
                title: this.ad.title,
                content: this.ad.content,
                display: this.ad.display,
                data: JSON.stringify(this.ad.data),
                broadcast_start: formatDate(this.ad.broadcastRange[0], {
                  outPattern: 'YYYY-MM-DD'
                }),
                broadcast_end: formatDate(this.ad.broadcastRange[1], {
                  outPattern: 'YYYY-MM-DD'
                })
              }
            });
            if (task.success) this.$message.success(this.$t('success'));else this.$message.error(this.$t('anErrorOccured'));
          } else {
            if (this.currentEditItem.pid !== this.ad.pid && this.tableData.findIndex(ad => ad.pid == this.ad.pid) > -1 && !parseInt(adId?.reusable)) {
              this.$message.error(this.$t('samePidError'));
              return false;
            }
            const newData = {};
            if (this.currentEditItem.pid !== this.ad.pid) newData['pid'] = this.ad.pid;
            if (this.currentEditItem.title !== this.ad.title) newData['title'] = this.ad.title;
            if (this.currentEditItem.content !== this.ad.content) newData['content'] = this.ad.content;
            if (this.currentEditItem.display != this.ad.display) newData['display'] = this.ad.display;
            if (this.currentEditItem.data !== JSON.stringify(this.ad.data)) newData['data'] = JSON.stringify(this.ad.data);
            const startDate = formatDate(this.ad.broadcastRange[0], {
                outPattern: 'YYYY-MM-DD'
              }),
              endDate = formatDate(this.ad.broadcastRange[1], {
                outPattern: 'YYYY-MM-DD'
              });
            if (this.currentEditItem.broadcast_start !== startDate) {
              newData['broadcast_start'] = startDate;
            }
            if (this.currentEditItem.broadcast_end !== endDate) {
              newData['broadcast_end'] = endDate;
            }
            if (!this.$_.isEmpty(newData)) {
              const task = await this.updateItemInDB({
                context: AD_CONTEXT,
                id: this.currentEditItem.id,
                data: newData
              });
              if (task.success) this.$message.success(this.$t('success'));else this.$message.error(this.$t('anErrorOccured'));
            }
          }
          this.cancelForm(ref);
          this.init();
        } else {
          return false;
        }
      });
    },
    cancelForm(ref) {
      this.$refs[ref].resetFields();
      this.adDialog = false;
    },
    updateAdData() {
      this.ad.data = [];
      if (this.ad.pid) {
        const adId = this.adsIds.find(adId => adId.id == this.ad.pid);
        if (adId) {
          JSON.parse(adId.params).forEach(param => {
            const obj = {
              name: param,
              value: ''
            };
            if (this.currentEditItem) {
              const data = JSON.parse(this.currentEditItem.data).find(d => d.name == param);
              if (data) obj.value = data.value;
            }
            this.ad.data.push(obj);
          });
        }
      }
    }
  }
};