import "core-js/modules/es.array.push.js";
import { reformatDate } from '@/utils/chronos';
import { API_REQUEST_HEADERS, STORAGE_API_BASE_URL } from '@/utils/constants';
import { MediaTypes, ThumbnailFormats } from '@/utils/enums';
import { capitalizeFirstLetter, compressImage, dataUrlToFile, humanFileSize } from '@/utils/functions';
import SettingsMixin from '@/mixins/settings';
import { MEDIA_CONTEXT } from '@/store/modules/database/media';
import { mapActions } from 'vuex';
export default {
  name: 'MediaLibrary',
  mixins: [SettingsMixin],
  props: {
    multiple: {
      type: Boolean,
      default: false
    },
    limit: {
      type: Number,
      default: 0
    },
    showImages: {
      type: Boolean,
      default: true
    },
    showVideos: {
      type: Boolean,
      default: true
    },
    showAudios: {
      type: Boolean,
      default: true
    },
    showDocuments: {
      type: Boolean,
      default: true
    },
    showOtherTypes: {
      type: Boolean,
      default: true
    },
    baseUrl: {
      type: String,
      default: STORAGE_API_BASE_URL
    },
    folder: {
      type: String,
      default: 'medias'
    },
    initSelectedMedias: {
      type: Array,
      default: null
    }
  },
  emits: ['validate'],
  data() {
    return {
      MediaTypes,
      users: [],
      medias: [],
      mediaDate: 0,
      mediaType: 0,
      mediaTypes: [],
      mediaDates: [],
      detailsData: [],
      mediaSearch: '',
      isLoading: true,
      showViewer: false,
      currentItem: null,
      lastMediaRowId: 0,
      selectedMedias: [],
      currentItemIndex: 0,
      imageEditorVisible: false,
      currentEditorDataUrl: null,
      showAudioReaderDialog: false,
      showVideoReaderDialog: false,
      imageEditorConfirmVisible: false
    };
  },
  computed: {
    filteredMedias() {
      let searchFilterResult = [];
      const typeFilterResult = this.medias.filter(m => this.mediaType == 0 || m.type == this.mediaType);
      if (this.mediaSearch) {
        searchFilterResult = typeFilterResult.filter(m => m.name.toLowerCase().includes(this.mediaSearch.toLowerCase()));
      } else {
        searchFilterResult = typeFilterResult;
      }
      const dateFilterResult = searchFilterResult.filter(m => this.mediaDate == 0 || this.extractDatetimeMonthYear(m.insert_datetime) == this.mediaDate);
      return dateFilterResult;
    },
    filteredMediaLinks() {
      const links = [];
      this.filteredMedias.forEach(media => {
        if (media.type == MediaTypes.IMAGE.value) links.push(this.resolveMediaLink(media.link));
      });
      return links;
    }
  },
  watch: {
    medias: {
      handler(val, oldVal) {
        this.loadMediaDates();
      },
      deep: true
    }
  },
  created() {
    this.$_.forIn(MediaTypes, (type, key) => {
      if (this.showImages && type.value == MediaTypes.IMAGE.value) this.mediaTypes.push(type);
      if (this.showAudios && type.value == MediaTypes.AUDIO.value) this.mediaTypes.push(type);
      if (this.showVideos && type.value == MediaTypes.VIDEO.value) this.mediaTypes.push(type);
      if (this.showOtherTypes && type.value == MediaTypes.OTHERS.value) this.mediaTypes.push(type);
      if (this.showDocuments && type.value == MediaTypes.DOCUMENT.value) this.mediaTypes.push(type);
    });
    this.mediaType = this.showImages && this.showVideos && this.showAudios && this.showDocuments && this.showOtherTypes ? 0 : this.mediaTypes[0].value;
    this.init();
  },
  methods: {
    ...mapActions('media', ['getMediasPage', 'deleteMedia', 'generateThumbnail']),
    ...mapActions('user', ['getAllUsers']),
    async init() {
      const resp0 = await this.getAllUsers();
      if (resp0.data) this.users = resp0.data;
      let data = null;
      const resp = await this.getMediasPage({
        offset: this.lastMediaRowId,
        limit: 18
      });
      if (resp.success && resp.data) {
        data = resp.data;
        this.lastMediaRowId += data.length;
        this.medias.push(...data);
        data.forEach(media => {
          if (this.initSelectedMedias && this.initSelectedMedias.includes(media.link)) this.selectedMedias.push(media);
        });
      } else {
        this.$message.error(this.$t('anErrorOccured'));
      }
      if (this.isLoading) this.isLoading = false;
      return data ? data.length : 0;
    },
    async infiniteHandler($state) {
      const newItemsLength = await this.init();
      if (newItemsLength > 0) $state.loaded();else $state.complete();
    },
    loadMediaDates() {
      this.mediaDates = [];
      this.medias.forEach(media => {
        const date = this.extractDatetimeMonthYear(media.insert_datetime);
        if (!this.mediaDates.includes(date)) this.mediaDates.push(date);
      });
    },
    extractDatetimeMonthYear(datetime) {
      if (!datetime) return '';
      let date = reformatDate(datetime.split(' ')[0], {
        outPattern: 'DD MMMM YYYY',
        locale: this.$i18n.locale
      });
      date = date.split(' ');
      return capitalizeFirstLetter(date[1]) + ' ' + date[2];
    },
    showPreview(item, index) {
      this.currentItem = item;
      this.currentItemIndex = index;
      if (item.type == MediaTypes.IMAGE.value) this.showViewer = true;
      if (item.type == MediaTypes.DOCUMENT.value) window.open(this.resolveMediaLink(item.link));
      if (item.type == MediaTypes.AUDIO.value) this.showAudioReaderDialog = true;
      if (item.type == MediaTypes.VIDEO.value) this.showVideoReaderDialog = true;
      if (item.type == MediaTypes.OTHERS.value) this.$message.warning(this.$t('cannotOpenThisFile'));
    },
    loadDetails(item, index) {
      this.currentItem = item;
      this.currentItemIndex = index;
      const type = this.mediaTypes.find(m => m.value == item.type);
      const date = reformatDate(item.insert_datetime.split(' ')[0], {
        outPattern: 'DD MMMM YYYY',
        locale: this.$i18n.locale
      });
      const sender = this.users.find(u => u.id == item.sender);
      this.detailsData = [{
        name: this.$t('name'),
        value: item.name
      }, {
        name: this.$t('url'),
        value: this.resolveMediaLink(item.link)
      }, {
        name: this.$t('type'),
        value: type ? type.label : ''
      }, {
        name: this.$t('size'),
        value: humanFileSize(item.size, true)
      }, {
        name: this.$t('mimeType'),
        value: item.mime
      }, {
        name: this.$t('sentBy'),
        value: sender ? sender.name : ''
      }, {
        name: this.$t('date'),
        value: date
      }];
      if (item.type == MediaTypes.IMAGE.value) {
        var img = new Image();
        img.onload = () => {
          const height = img.naturalHeight;
          const width = img.naturalWidth;
          if (width && height && this.detailsData.length) {
            this.detailsData.push({
              name: this.$t('dimensions'),
              value: width + ' x ' + height
            });
          }
        };
        img.src = this.resolveMediaLink(item.link);
      }
    },
    async reset() {
      this.isLoading = true;
      this.medias = [];
      this.detailsData = [];
      this.selectedMedias = [];
      this.lastMediaRowId = 0;
      await this.init();
    },
    copyLink(link, success) {
      if (success) this.$message.success(this.$t('mediaLinkCopied'));
    },
    updateSelectedMedias(item, index) {
      const i = this.selectedMedias.findIndex(m => m.id === item.id);
      if (i > -1) {
        this.selectedMedias.splice(i, 1);
        this.detailsData = [];
      } else {
        if (this.multiple) this.selectedMedias.push(item);else this.selectedMedias = [item];
        this.loadDetails(item, index);
      }
    },
    deleteItem() {
      this.$confirm(this.$t('deleteMediaQuestion'), this.$t('delete'), {
        confirmButtonText: this.$t('yes'),
        cancelButtonText: this.$t('cancel'),
        type: 'warning'
      }).then(async () => {
        const task = await this.deleteMedia({
          id: this.currentItem.id
        });
        if (task.success) {
          this.$message.success(this.$t('success'));
          this.reset();
        } else {
          this.$message.error(this.$t('anErrorOccured'));
        }
      }).catch(() => {
        /* empty */
      });
    },
    validate() {
      this.$emit('validate', this.multiple ? this.selectedMedias : this.selectedMedias[0], this.multiple);
    },
    storeEditedPicture(result, dataUrl) {
      this.currentEditorDataUrl = dataUrl;
      this.imageEditorConfirmVisible = true;
    },
    async saveEditedPicture(overwrite) {
      if (this.currentEditorDataUrl) {
        var file = dataUrlToFile(this.currentEditorDataUrl, {
          filename: this.$uuidv4() + '.png',
          type: 'image/png'
        });
        if (this.compressImages) file = await compressImage(file, {
          maxSizeMB: this.compressThreshold
        });
        const newFileName = overwrite ? this.currentItem.name : file.name;
        const formData = new FormData();
        formData.append('folder', this.folder);
        formData.append('overwrite', overwrite);
        formData.append('file', file, newFileName);
        try {
          const task = await this.axios.post(this.baseUrl, formData, {
            headers: API_REQUEST_HEADERS
          });
          if (task.data && task.data['status'] == 'success') {
            if (overwrite) {
              await this.deleteMedia({
                id: this.currentItem.id
              });
            }
            await this.addItemInDB({
              context: MEDIA_CONTEXT,
              data: {
                name: newFileName,
                link: 'root/' + this.folder + '/' + newFileName,
                type: MediaTypes.IMAGE.value,
                size: file.size || 0,
                mime: file.type || '',
                sender: this.userData.id
              }
            });
            await this.generateThumbnail({
              autoWidth: this.thumbnailsFormat == ThumbnailFormats.AUTOWIDTH.value,
              autoHeight: this.thumbnailsFormat == ThumbnailFormats.AUTOHEIGHT.value,
              folder: this.folder,
              file: newFileName,
              lib: this.imgProcessingLib,
              models: this.thumbnails.filter(thumbnail => thumbnail.label && thumbnail.width && thumbnail.height)
            });
            this.imageEditorConfirmVisible = false;
            this.imageEditorVisible = false;
            await this.reset();
          } else {
            this.$message.error(this.$t('anErrorOccured'));
          }
        } catch (error) {
          this.$message.error(this.$t('anErrorOccured'));
        }
      }
    }
  }
};