import slugify from 'slugify';
import { reformatDate } from '@/utils/chronos';
import AccessManager from '@/mixins/access-manager';
import SettingsMixin from '@/mixins/settings';
import ViewsBaseMixin from '@/mixins/views-base';
import { mapActions } from 'vuex';
export default {
  name: 'Identifiers',
  mixins: [ViewsBaseMixin, AccessManager, SettingsMixin],
  data() {
    return {
      errorMessage: '',
      currentAddItem: {
        id: '',
        reusable: 0,
        params: []
      },
      currentEditItem: null,
      uploadDialog: false,
      isLoading: false,
      tableData: [],
      pageSize: 6,
      search: '',
      total: 0,
      page: 1
    };
  },
  computed: {
    pagedTableData() {
      this.total = this.searching.length;
      return this.searching.slice(this.pageSize * this.page - this.pageSize, this.pageSize * this.page);
    },
    searching() {
      if (!this.search) {
        this.total = this.tableData.length;
        return this.tableData;
      }
      this.page = 1;
      return this.tableData.filter(data => data.id.toLowerCase().includes(this.search.toLowerCase()));
    }
  },
  watch: {
    'currentAddItem.id': {
      handler(val) {
        if (val) this.currentAddItem.id = slugify(val, {
          trim: true,
          lower: true,
          strict: true
        });
      }
    },
    'currentEditItem.id': {
      handler(val) {
        if (val) this.currentEditItem.id = slugify(val, {
          trim: true,
          lower: true,
          strict: true
        });
      }
    }
  },
  created() {
    this.init();
  },
  methods: {
    ...mapActions('ad', ['getAllIdentifiers', 'addIdentifier', 'updateIdentifier', 'deleteIdentifier']),
    reformatDate,
    setPage(val) {
      this.page = val;
    },
    indexMethod(index) {
      return index + (this.page - 1) * this.pageSize + 1;
    },
    async init() {
      this.isLoading = true;
      const resp = await this.getAllIdentifiers();
      if (resp.data) this.tableData = resp.data.reverse().map(obj => ({
        ...obj,
        fakeId: this.$uuidv4()
      }));
      if (resp.failure) this.$message.error(this.$t('anErrorOccured'));
      this.isLoading = false;
    },
    editItem(index, item) {
      this.currentEditItem = this.$_.cloneDeep(item);
      this.currentEditItem.params = JSON.parse(item.params);
      this.currentEditItem.reusable = parseInt(item.reusable);
    },
    async submitEdit(index, item) {
      if (!this.currentEditItem || !this.currentEditItem.id) return;
      if (item.id != this.currentEditItem.id && this.tableData.findIndex(e => e.id == this.currentEditItem.id) > -1) {
        this.$message.warning(this.$t('idAlreadyExists'));
        return;
      }
      const task = await this.updateIdentifier({
        id: item.id,
        data: {
          id: this.currentEditItem.id,
          reusable: this.currentEditItem.reusable,
          params: JSON.stringify(this.currentEditItem.params)
        }
      });
      if (task.success) {
        this.currentEditItem = null;
        this.init();
      } else this.$message.error(this.$t('anErrorOccured'));
    },
    async deleteItem(index, item) {
      const task = await this.deleteIdentifier(item.id);
      if (task.success) this.init();else this.$message.error(this.$t('anErrorOccured'));
    },
    async submit() {
      this.errorMessage = '';
      if (!this.currentAddItem.id) {
        this.errorMessage = this.$t('fieldRequired');
        return;
      }
      if (this.tableData.findIndex(e => e.id == this.currentAddItem.id) > -1) {
        this.$message.warning(this.$t('idAlreadyExists'));
        return;
      }
      const task = await this.addIdentifier({
        id: this.currentAddItem.id,
        reusable: this.currentAddItem.reusable,
        params: JSON.stringify(this.currentAddItem.params)
      });
      if (task.success) {
        this.init();
        this.currentAddItem = {
          id: '',
          reusable: 0,
          params: []
        };
        this.$message.success(this.$t('success'));
      } else this.$message.error(this.$t('anErrorOccured'));
    }
  }
};